.nav-modal {
  height: 300px;
  width: 95%;
  background-color: white;
  box-shadow: 0px 0px 7px 2px rgba(0, 0, 0, 0.25);
  position: absolute;
  top: 10px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  border-radius: 25px;
  display: none;
  transition: all 0.3s ease;
  z-index: 3;
}

.nav-modal.active {
  animation: scale-display 0.3s;
  display: block;
}

.nav-modal.out {
  animation: scale-display--reversed 0.3s;
  animation-fill-mode: forwards;
  display: inline-flex;
}

@keyframes scale-display {
  0% {
    opacity: 0;
    transform: scale(0);
    -webkit-transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

@keyframes scale-display--reversed {
  0% {
    display: inline-flex;
    opacity: 1;
    transform: scale(1);
    -webkit-transform: scale(1);
  }
  99% {
    display: inline-flex;
    opacity: 0;
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    display: none;
    opacity: 0;
    transform: scale(0);
    -webkit-transform: scale(0);
  }
}

.nav-close {
  float: left;
  padding-left: 20px;
  padding-top: 10px;
}

.modal-nav-list {
  list-style: none;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
  font-family: paytoneOne;
  padding-right: 65px;
}
