.card-content {
  background-color: white;
}

.card-content > * {
  font-family: patuaOne;
}

.discovery-card {
  background: #f7fafc;
  box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.35);
  border-radius: 8px;
  cursor: pointer;
}

@media screen and (max-width: 526px) {
  .landing-card {
    min-width: 220px;
  }
}
